/* –û–±—â–∏–µ —Å—Ç–∏–ª–∏ –¥–ª—è —Ñ–æ—Ä–º—ã –≤—Ö–æ–¥–∞ –∏ –≤—ã—Ö–æ–¥–∞ */
.login-form,
.logout-section {
    max-width: 400px;
    margin: 40px auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 10px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    background: white;
    text-align: center;
}

/* –ó–∞–≥–æ–ª–æ–≤–æ–∫ */
.login-form h2,
.logout-section h2 {
    margin-bottom: 20px;
    font-size: 1.5rem;
}

/* –ò–Ω–ø—É—Ç—ã */
.login-form input[type="email"],
.login-form input[type="password"] {
    width: 100%;
    padding: 10px;
    margin: 8px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1rem;
}

/* –ß–µ–∫–±–æ–∫—Å */
.login-form label {
    display: inline-flex;
    align-items: center;
    font-size: 0.9rem;
}

/* –ö–Ω–æ–ø–∫–∏ */
.login-form button,
.logout-section button {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #5c9ded;
    border: none;
    color: white;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.2s;
}
.login-form button:hover,
.logout-section button:hover {
    background-color: #3a7cd8;
}

/* –ú–æ–±–∏–ª—å–Ω–∞—è –∞–¥–∞–ø—Ç–∏–≤–∫–∞ */
@media (max-width: 480px) {
    .login-form,
    .logout-section {
        margin: 20px 8px;
        padding: 16px;
    }
    .login-form h2,
    .logout-section h2 {
        font-size: 1.25rem;
    }
    .login-form input[type="email"],
    .login-form input[type="password"] {
        padding: 8px;
        font-size: 0.9rem;
    }
    .login-form button,
    .logout-section button {
        width: 100%;
        padding: 12px;
        font-size: 1rem;
    }
}
